export function PWD( form ){
    const PWD = form['password'];
    const confirmPWD = form['confirmPassword'];
    let msg = "";
    if( PWD!="" && PWD == confirmPWD) {
        let re;
        if(PWD.length < 4) {
            msg = "Error: Password must contain at least six characters!";
        }
        re = /[0-9]/;
        if(!re.test(PWD)) {
            msg = "Error: password must contain at least one number (0-9)!";
        }
        re = /[a-z]/;
        if(!re.test(PWD)) {
            msg = "Error: password must contain at least one lowercase letter (a-z)!";
        }
        re = /[A-Z]/;
        if(!re.test(PWD)) {
            msg = "Error: password must contain at least one uppercase letter (A-Z)!";
        }
        return {
            status: false,
            msg
        }
    } else {
        msg = "Error: Please check that you've entered and confirmed your password!";
        return {
            status: false,
            msg
        }
    }
    return {
        status: true,
        msg
    }
}


